.church_bg {

    background-color: grey;
  
    color: white;

}

.church_outer {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    padding: 40px;
}

#church_footer_social i {
    padding: 5px;
    font-size: 38px;
}

.church_inner {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.map_button:hover {
    background-color: #aa824e;

}

#church_footer_social {
    display: flex;
}