.worship_bg {
   background-image: url('../../images/worship.png') ;
   max-width: 100%;
   background-repeat: no-repeat;
   background-size: cover;
   background-attachment: fixed; 
   background-position: center center;
   position: relative;
   height: 100%;
}


.worship_pic {
   height: 100%;
    width: 100%;
}


.font-def-heading1 {
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: 600;
    font-size: 61.0px;
    font-style: normal;
    line-height: 1.15em;
    letter-spacing: 0.0em;
}

.worship_text {
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    background-color: white;
    opacity: 0.7;
    /* color: white; */
    border: #aa824e 5px solid;
    height: 100%;
    padding: 35px;
}

.svg_icons {
    width: 100px;
    height: 100px;
}

.svg_icons1 {
    width: 200px;
    height: 100px;
}