#hero_container {
    width: 100%;
    height: 100dvh;
    overflow: hidden;
    position: relative;
    top: 0;
    bottom: 0;
}

#videotest {
    min-width: 100%;
    min-height: 100%;
    height: auto;
    width: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#centered {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: grid;
    place-items: center;
    text-align: center;
  }

