
#hero_container1 {
    width: 100%;
    height: 100dvh;
    overflow: hidden;
    position: relative;
    top: 0;
    bottom: 0;
    /* background: black; */
    overflow: hidden;
}

#video {
    min-width: 100%;
    min-height: 100%;
    height: auto;
    width: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
    background-position: center;
}


#centered1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: grid;
    place-items: center;
    text-align: center;
  }
