#connecthero_container {
    width: 100%;
    height: 100dvh;
    overflow: hidden;
    position: relative;
    top: 0;
    bottom: 0;
    overflow: hidden;
}


#connect-hero-video {
    min-height: 100%;
    max-width: 100%;
    min-width: 100%;
    
}

#centered-connect-hero {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    display: grid;
    place-items: center;
    text-align: center;
  }